














import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';
import { IListItem } from '@/types/interface';

@Component
export default class BaseSearchSelection extends Vue {
  @Prop({ default: () => [] }) list!: IListItem[] | [];
  @Model('change', { default: () => [] }) readonly checked!: [string];

  filterTxt = '';
  isShow = false;

  get filterList() {
    if (this.filterTxt === '') return this.list;
    return this.list.filter((item: IListItem) => item.val.indexOf(this.filterTxt) >= 0);
  }

  get checkedList() {
    return this.checked;
  }

  set checkedList(list) {
    this.$emit('change', list);
  }
}
